.label {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.spanClassName {
    /*border: 1px solid red;*/
    font-family: Ubuntu, sans-serif;
}

.checkbox {
    appearance: none;
    width: 25px;
    height: 25px;
    margin-right: 5px;
    border: 2px solid #282c34;
    border-radius: 50%;
}

.checkbox:checked {
    appearance: none;
    background: url('checkIcon.png') 0/cover no-repeat;
}